<style scoped>
.signin {
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    text-align: center;
}

</style>
<template>
    <el-table :data="tableData" border style="width: 100%" stripe height="800px">
        <el-table-column fixed prop="ID" label="ID" width="50">
        </el-table-column>
        <el-table-column prop="name" label="Name" >
        </el-table-column>
        <el-table-column prop="port" label="Port" width="60">
        </el-table-column>
        <el-table-column prop="user" label="User">
        </el-table-column>
        <el-table-column prop="type" label="Type">
        </el-table-column>
        <el-table-column label="UpdatedAt" width="180">
            <template slot-scope="scope">
               {{scope.row.UpdatedAt.substr(0,19)}}
            </template>
        </el-table-column>
        <el-table-column fixed="right" label="Action" width="200" >
            <template slot-scope="scope">
                <el-button-group>
                <el-button
                    @click="handleClickConsole(scope.row);"
                    type="primary"
                    size="small"
                    icon="el-icon-video-play"
                    ></el-button>
                <el-button
                        @click="handleClickConsole(scope.row);"
                        type="warning"
                        size="small"
                        icon="el-icon-edit"
                ></el-button>
                <el-button
                        @click="handleClickConsole(scope.row);"
                        type="success"
                        size="small"
                        icon="el-icon-monitor"
                ></el-button>
                <el-button
                        @click="handleClickConsole(scope.row);"
                        type="danger"
                        size="small"
                        icon="el-icon-delete-solid"
                ></el-button>
                </el-button-group>

            </template>
        </el-table-column>
    </el-table>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            q: ""
        };
    },
    mounted() {
        this.fetchSshList(this.q);
    },
    created() {},
    methods: {
        fetchSshList(q) {
            this.$http
                .get("api/ssh/list", { params: { q } })
                .then(resp => {
                    this.tableData = resp.data
                })
        },
        handleClickConsole(row){
            this.$router.push({'name':'sshConsole',query:{id:row.ID}})
        }
    }
};
</script>
